import React from 'react'
import {Row} from 'react-bootstrap';
import video from '../assets/warehouse-video.mp4';
import poster from '../assets/products-poster.jpg';
import image from '../assets/inventory.jpg';
import products from '../assets/products.jpg';
import ScrollAnimation from 'react-animate-on-scroll';
import product1 from '../assets/product1.jpg';
import product2 from '../assets/product2.jpg';
import product3 from '../assets/product3.jpg';
import product4 from '../assets/product4.jpg';
import product5 from '../assets/product5.jpg';
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";


export default function Products(props) {
  const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
      slidesToSlide: 3 // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
      slidesToSlide: 2 // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1 // optional, default to 1.
    }
  };

  return (
    // <Container>
    <Row>
 

      <div className="video-container">
        <h1>Our Products</h1>

        <video className='videoTag' autoPlay loop muted poster={poster}>
          <source src={video} type='video/mp4' />

        </video>
      </div>

      <Carousel
        swipeable={false}
  draggable={false}
  showDots={false}
  arrows={false}
  responsive={responsive}
  ssr={true} // means to render carousel on server-side.
  infinite={true}
  autoPlay={props.deviceType !== "mobile" ? true : false}
  autoPlaySpeed={4000}
  keyBoardControl={true}
  // customTransition="all .5"
  transitionDuration={500}
  containerClass="carousel-container"
  removeArrowOnDeviceType={["tablet", "mobile"]}
  deviceType={props.deviceType}
  dotListClass="custom-dot-list-style"
  itemClass="carousel-item-padding-40-px"
      >
  <div>
    <img
      className="carousel-img"
      src={product1}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={product2}
      alt="Second slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={product3}
      alt="Third slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={product4}
      alt="Fourth slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={product5}
      alt="Fifth slide"
    />
  </div>

</Carousel>
      <div class="info--container">
      <ScrollAnimation animateIn="fadeIn">

        <div className="about">
          <div className="text">
            <p>Chaskancha sells an assortment of high quality products at affordable proices.  A full list of the producst we provide will be available soon.</p>
          </div>
          <div className="image">
            <img src={image} />
          </div>
        </div>
        </ScrollAnimation>

        <ScrollAnimation animateIn="fadeIn">

        <div className="about">
        <div className="image">
            <img src={products} />
          </div>
          <div className="text">
            <p>Quality. That's our primary concern.  We want our customers to know that the products they purchase from us are sourced only from the highest quality suppliers.  We will never compromsie if it means our customers receive an inferior product.</p>
          </div>

        </div>
        </ScrollAnimation>
 
      </div>




    </Row>

    // </Container>

  )
}
