import React from 'react'
import { Container, Row, Col, Image } from 'react-bootstrap';
import video from '../assets/kitchen-needs.mp4';
import poster from '../assets/about-poster.jpg';
import image from '../assets/about-us-business.jpg';
import vision from '../assets/vision.jpg';
import ScrollAnimation from 'react-animate-on-scroll';


export default function About() {

  // let height = document.getElementsByTagName('h1').height;
  // height = height / 2;

  // let h1 = document.querySelector('.video-container h1')

  // // h1.style.setProperty("top", "calc(50% - " + height + "px)");
  // h1.style.top = "calc(50% - " + height + "px)";

  return (
    // <Container>
    <Row>

      <div className="video-container">
        <h1>About Us</h1>
        <video className='videoTag' autoPlay loop muted poster={poster}>
          <source src={video} type='video/mp4' />

        </video>

      </div>
      <div class="info--container">
      <ScrollAnimation animateIn="fadeIn">

        <div className="about">
          <div className="text">
            <p>Chaskancha Inc.was founded in 2021 with the goal of providing the highest quality beauty and apparel products.Our founder, Angelica Guzman, has been designing apparel for many years and has a deep understanding of the
              online retail industry.Her goal is so sell, at the best prices, only the finest quality products that she would use herself.</p>
          </div>
          <div className="image">
            <img src={image} />
          </div>
        </div>
        </ScrollAnimation>

        <ScrollAnimation animateIn="fadeIn">

        <div className="about">
          <div className="image">
            <img src={vision} />
          </div>
          <div className="text">
            <p>Our vision is to source only the highest quality products from the most trusted resllers in the business.We have a global outlook, and hope to be providing only the higuest quality products to markets in Europe, North and South America, and beyond.</p>
          </div>
        </div>
        </ScrollAnimation>

      </div>




    </Row>

    // </Container>
  )
}
