import React from 'react'
import { Container, Row, Col, Image } from 'react-bootstrap';
import video from '../assets/mission-video.mp4';
import poster from '../assets/mission-poster.jpg';
import image from '../assets/goals-image.jpg';
import supplier from '../assets/supplier.jpg';
import ScrollAnimation from 'react-animate-on-scroll';

export default function Mission() {


  return (
    // <Container>
    <Row>
      <div className="video-container mission-container">
        <h1>Mission Statement</h1>

        <video className='videoTag' autoPlay loop muted poster={poster}>
          <source src={video} type='video/mp4' />

        </video>

      </div>



      <div class="info--container">
      <ScrollAnimation animateIn="fadeIn">

        <div className="about">
          <div className="text">

            <p>Our mission statement is as follows: Provide the highest quality, best-in-class products at prices that can't be beat.  Our focus is customer-first driven.  If our customers
              are not happy, then neither are we.  This is why we strive to provide 100% satisfaction, and addressing customer concerns is our top priority.</p>
          </div>
          <div className="image">
            <img src={image} />
          </div>
        </div>
        </ScrollAnimation>
        <ScrollAnimation animateIn="fadeIn">

        <div className="about">
        <div className="image">
            <img src={supplier} />
          </div>
          <div className="text">
            <p>Building trust with the suppliers we work with is paramount. Our goal is to establish a solid foundation of trust and mutual benefit with all suppliers we work with. If the companies we work with aren't satisfied, than neither are we.</p>
          </div>

        </div>
        </ScrollAnimation>

      </div>




    </Row>

    // </Container>
  )
}
